/** @format */

.Plans {
  background-color: #fff;
  width: 100%;
  font-family: Roboto;
  text-align: center;
}

.Plans main {
  background-color: #fff;
  border-top-left-radius: 24px;
  border-top-right-radius: 24px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  box-shadow: 0 -4px 5px 0 rgb(0 0 0 / 8%);
  height: 100%;
}

.Logo_Section {
  padding: 40px;
  width: 100%;
}

.Logo_Section svg {
  margin: auto;
}

.Form_Header {
  padding-top: 32px;
  display: grid;
  grid-template-columns: 15% 80%;
}

.Form_Header .Back_Btn {
  align-self: center;
}

.Form_Header .Header_Text h1 {
  text-align: start;
  font-size: 24px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.17;
  letter-spacing: normal;
  color: #3b414a;
}

/* Pricing Table */

.Plan_Section {
  margin-top: 24px;
}

.Plan_Section .Table_Head,
.Table_Content,
.Table_Price {
  width: 98%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 3px;
  margin: auto;
}

.Plan_Section .Table_Head div {
  width: 100%;
  border-radius: 9px;
  background-color: #f9f9f9;
  padding: 15px;
}

.Plan_Section .Table_Head div:nth-child(2) {
  border-top: 6px solid #f7b500;
}

.Plan_Section .Table_Head div:nth-child(3) {
  border-top: 6px solid #44d7b6;
}
.Plan_Section .Table_Head div:nth-child(4) {
  border-top: 6px solid #12b0ee;
}

.Table_Head div h1 {
  font-family: Roboto;
  font-size: 16px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 0.88;
  letter-spacing: normal;
  text-align: center;
  color: #24272c;
}

.Table_Content {
  margin-top: 20px;
}

.Table_Content div p {
  font-family: Roboto;
  font-size: 16px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 0.88;
  letter-spacing: normal;
  text-align: center;
  color: #24272c;
}

.button {
  padding: 0 !important;
  justify-content: center !important;
  margin-top: 25px !important;
}
